import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { debounceTime, filter, throttleTime } from 'rxjs/operators';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { FetchapiService } from 'src/app/services/fetchapi.service';
import { DatePipe, Location } from '@angular/common'

@Component({
  selector: 'app-reviewcomments',
  templateUrl: './reviewcomments.component.html',
  styleUrls: ['./reviewcomments.component.css']
})
export class ReviewcommentsComponent implements OnInit {
  @ViewChild('scrollMe', { static: false }) private myScrollContainer: ElementRef;

  @Input() chatId: string;

  id: any
  messageControl: FormControl;
  chatForm: FormGroup;
  index: number = null;
  result = Array();
  message: any;
  listReviews: any[];
  review: any[];
  currentUser: string;
  date: Date;

  constructor(
    private spinner: NgxSpinnerService,
    private fb: FormBuilder,
    private service: FetchapiService,
    public datepipe: DatePipe,
    private location: Location
  ) {
    this.messageControl = new FormControl();
    // this.chatForm = this.fb.group({ message: this.messageControl });
  }

  ngOnInit() {
    this.currentUser = localStorage.getItem("userId");
    this.chatForm = new FormGroup({
      message: new FormControl('', Validators.required),
    }
    );
    this.scrollBottom();
    this.scrollToBottom();
    this.reviewlist();
  }
  ngAfterViewChecked() {        
    this.scrollToBottom();        
} 

scrollToBottom(): void {
    try {
        this.myScrollContainer.nativeElement.scrollTop = this.myScrollContainer.nativeElement.scrollHeight;
    } catch(err) { }                 
}


  private scrollBottom(): void {
    setTimeout(() => window.scrollTo(0, document.body.scrollHeight), 500);
  }

  back() {
    this.location.back();
  }

  reviewlist() {
    debugger
    // this.spinner.show();
    var newdata = {
      'userId': localStorage.getItem("userId"),
      "PO_No": localStorage.getItem("PO_No"),
      "Page": localStorage.getItem("Page"),
    }
    var data = {
      "data": {
        "content": btoa(JSON.stringify(newdata)),
        "encryptCode": ""
      },
      "globalInfo": {
        "latitude": "2.32.34.00",
        "langitude": "12.232.12",
        "deviceNo": "Hp",
        "userName": localStorage.getItem("username"),
        "requestTime": new Date(),
        "interfaceCode": "I032",
        "UserCategory": localStorage.getItem("userCategory"),
        "userId": localStorage.getItem("userId")
      },
      "returnStateInfo": {
        "returnMessage": "",
        "returnCode": ""
      }
    }

    this.service.GetReviewList(data).subscribe((res: any) => {
      debugger
      this.spinner.hide();
      var data = JSON.parse(res);
      if (data.returnStateInfo.returnMessage == 'Success') {
        debugger
        var response = atob(data.data.content);
        if (response == '') {
          // return alert("No Comments");
        }
        else {
          this.listReviews = JSON.parse(response);
          // this.date=this.listReviews.datetime[this.index]
          // let latest_date =this.datepipe.transform(this.date, 'yyyy-MM-dd');
        }
      }
    });
  }

  submit(): void {
    debugger
    this.spinner.show();
    var reviewdata = {
      'userId': localStorage.getItem("userId"),
      "PO_No": localStorage.getItem("PO_No"),
      "Page": localStorage.getItem("Page"),
      // "comment": this.chatForm.value.message.replace(/\n/g, ' '),
      "comment": this.chatForm.value.message.replace(/\n/g, ' '),
    }

    
    // "userid": localStorage.getItem("userId") ,
    // "datetime": new Date(),
    // "chat": Chat

    var data = {
      "data": {
        "content": btoa(JSON.stringify(reviewdata)),
        "encryptCode": ""
      },
      "globalInfo": {
        "latitude": "2.32.34.00",
        "langitude": "12.232.12",
        "deviceNo": "Hp",
        "userName": localStorage.getItem("username"),
        "requestTime": new Date(),
        "interfaceCode": "I033",
        "UserCategory": localStorage.getItem("userCategory"),
        "userId": localStorage.getItem("userId")
      },
      "returnStateInfo": {
        "returnMessage": "",
        "returnCode": ""
      }
    }
    this.clearMsg();
    this.service.AddReview(data).subscribe((res: any) => {
      debugger
      this.spinner.hide();
      var data = JSON.parse(res);
      if (data.returnStateInfo.returnMessage == 'Success') {
        debugger
        this.review = JSON.parse(atob(data.data.content));
        this.clearMsg();
        this.reviewlist();
        // alert(this.review.responseModel.msg);
      }
    });
    this.scrollBottom();
  }

  clearMsg() {
    //this.chatForm.value.message = "";
    this.chatForm.reset();
  }

}
