
import { AfterViewInit, Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import { Router } from '@angular/router';
import * as faceapi from 'face-api.js';
import { NgxSpinnerService } from 'ngx-spinner';
import { DialogService } from 'src/app/services/dialog.service';
import { FetchapiService } from 'src/app/services/fetchapi.service';

faceapi.tf.getBackend()

@Component({
  selector: 'app-record-face',
  templateUrl: './record-face.component.html',
  styleUrls: ['./record-face.component.css']
})
export class RecordFaceComponent implements OnInit {

  imageSrc;


  @ViewChild("video" , {static:false})
  public video: ElementRef;

  @ViewChild("canvas" , {static:false})
  public canvas: ElementRef;

  public captures: Array<any>;

  public constructor(
    public dialog:DialogService,private router: Router,
    private service:FetchapiService,
    private spinner: NgxSpinnerService,
    )
    {  this.captures = []; }

  public ngOnInit() { }

  public ngAfterViewInit() { debugger

    var constraints = {
      audio: false,
      video: {
          width: { ideal: 1280 },
          height: { ideal: 1024 },
          facingMode: {exact : 'user'}
      }
  };

      if(navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
          navigator.mediaDevices.getUserMedia(constraints).then(stream => {
              this.video.nativeElement.srcObject = stream;
              this.video.nativeElement.play();
          })
          .catch(err=>{
            console.log("unhanled error: "+ err);
            alert("unhanled error: "+ err)
          });
      }
  }

  public capture() { debugger
      var context = this.canvas.nativeElement.getContext("2d").drawImage(this.video.nativeElement, 0, 0, 640, 480);
      //this.captures.push(this.canvas.nativeElement.toDataURL("image/png"));
      this.imageSrc = this.canvas.nativeElement.toDataURL("image/png") ;  // get base64
  }




// download(base64)
// {debugger
//     var a = document.createElement("a"); //Create <a>
//     a.href = base64 //Image Base64 Goes here
//     a.download = "Image.png"; //File name Here                            
//     a.click(); //Downloaded file

// }

  async uploadFaceData()
  {   debugger
    this.spinner.show();

    const mtcnnParams = {
      // number of scaled versions of the input image passed through the CNN
      // of the first stage, lower numbers will result in lower inference time,
      // but will also be less accurate
      maxNumScales: 10,
      // scale factor used to calculate the scale steps of the image
      // pyramid used in stage 1
      scaleFactor: 0.709,
      // the score threshold values used to filter the bounding
      // boxes of stage 1, 2 and 3
      scoreThresholds: [0.6, 0.7, 0.7],
      // mininum face size to expect, the higher the faster processing will be,
      // but smaller faces won't be detected
      minFaceSize: 50
    }
    // const options = new faceapi.MtcnnOptions(mtcnnParams)

   const img = await faceapi.fetchImage( this.imageSrc )
    const fullFaceDescription = await faceapi.detectSingleFace(img, new faceapi.TinyFaceDetectorOptions()).withFaceLandmarks().withFaceDescriptor()

  if (!fullFaceDescription) {
    this.spinner.hide();
    this.dialog.openDialog('Not Getting face data , Please Try Again','Error');
    //throw new Error(`no faces detected`);
    return false;
  }
  else
  {
    var faceDescriptors = [fullFaceDescription.descriptor];
    var str = '['+faceDescriptors[0]+']';


    var a ={
    "userId": localStorage.getItem("userId"),
    "FaceArray": str,
    "Img": this.imageSrc.split(',')[1] // base64 Image
         }

   var enc = btoa(JSON.stringify(a));

 var data = {
    "data": {
    "content": enc,
    "encryptCode": ""
    },
    "globalInfo": {
    "latitude": "2.32.34.00",
    "langitude": "12.232.12",
    "deviceNo": "Hp",
    "userName": localStorage.getItem("username"),
    "requestTime": new Date(),
    "interfaceCode": "I023",
    "UserCategory": localStorage.getItem("userCategory"),
    "userId": localStorage.getItem("userId")
    },
    "returnStateInfo": {
    "returnMessage": "",
    "returnCode": ""
    }
   }


    this.service.recordFace(data).subscribe((res:any) =>{debugger
     var data = JSON.parse(res);
    var R = JSON.parse(atob(data.data.content));
    if(data.returnStateInfo.returnMessage == 'Success' && R.responseModel.status == true)
    {
       this.dialog.openDialog(R.responseModel.msg,'Success')
       this.router.navigate(['/userprofile']);
    }
    else
    {
      this.dialog.openDialog(R.responseModel.msg,'Error')
    }

    this.spinner.hide();

   });





  }

}






}
