  protected filterBanks() {
    debugger
    if (!this.banks) {
      return;
    }
    // get the search keyword
    let search = this.dropdown_search.value;
    if (!search) {
      this.document_list_data.next(this.DocumentList.slice());
      return;
    } else {
      search = search.toLowerCase();
    }
    // filter the banks
    this.document_list_data.next(
      this.DocumentList.filter(item => item.documentName.toLowerCase().indexOf(search) > -1)
    );
  }

