import { Component, OnInit ,ViewChild, TemplateRef, ElementRef, AfterViewInit} from '@angular/core';
 import { Idle, DEFAULT_INTERRUPTSOURCES } from '@ng-idle/core';
import { Keepalive } from '@ng-idle/keepalive';
import * as faceapi from 'face-api.js';
import { NavigationEnd, Router ,Event} from '@angular/router';


// import { BsModalService } from 'ngx-bootstrap/modal';
// import { BsModalRef } from 'ngx-bootstrap/modal';
// import { ModalDirective } from 'ngx-bootstrap/modal';
import { DialogService } from './services/dialog.service';
import { any } from '@tensorflow/tfjs';
import { NgxSpinnerService } from 'ngx-spinner';
import { FetchapiService } from './services/fetchapi.service';
import { ToastrService } from 'ngx-toastr';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent{

  idleState = 'Not started.';
  timedOut = false;
  lastPing?: Date = null;
  title = 'angular-idle-timeout';

  // public modalRef: BsModalRef;

  // @ViewChild('childModal', { static: false }) childModal: ModalDirective;

  constructor(private idle: Idle, private keepalive: Keepalive,
    private router: Router,  // private modalService: BsModalService,
    public dialog:DialogService,
    private service:FetchapiService,
  private spinner: NgxSpinnerService,
  private toastr: ToastrService
    ) { debugger
     // sets an idle timeout of 5 seconds, for testing purposes.
    idle.setIdle(500);
    // sets a timeout period of 5 seconds. after 10 seconds of inactivity, the user will be considered timed out.
    idle.setTimeout(500);
    // sets the default interrupts, in this case, things like clicks, scrolls, touches to the document
    idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);

    idle.onIdleEnd.subscribe(() => {
      this.idleState = 'No longer idle.'
      console.log(this.idleState);
      this.reset();
    });

    idle.onTimeout.subscribe(() => {
      // this.childModal.hide();
      this.idleState = 'Timed out!';
      this.timedOut = true;
      console.log(this.idleState);
      // localStorage.clear();
      // sessionStorage.clear();
      // this.router.navigate(['/login']);

      // this.dialog.openDialog('You have been log-out because of Inactivity','Alert');
      // this.dialog.openDialog('Session timeout','Alert');
      this.Logout();
      location.reload();
    });

    idle.onIdleStart.subscribe(() => {
        this.idleState = 'You\'ve gone idle!'
        console.log(this.idleState);
        // this.childModal.show();
    });

    idle.onTimeoutWarning.subscribe((countdown) => {
     // this.dialog.openDialog('You will time out in ' + countdown + ' seconds!','Alert');
      this.idleState = 'You will time out in ' + countdown + ' seconds!'

      console.log(this.idleState);
    });

    // sets the ping interval to 15 seconds
    keepalive.interval(15);

    keepalive.onPing.subscribe(() => this.lastPing = new Date());

  //        //this.reset();



 router.events.subscribe((event: Event) => {
   if (event instanceof NavigationEnd) {
            // Navigation Ended Successfully.
            var url= event.url
            url = url.toLowerCase();
            if(url == '/login')
            {
              idle.stop();
              this.idleState = 'Stopped.';
            }
            else{
              idle.watch()
              this.timedOut = false;
              this.idleState = 'Started.';
            }
       }
   });





  }





  ngOnInit(): void {
    
// this.loadModels();
  }

  reset() {
    this.idle.watch();
    this.idleState = 'Started.';
    this.timedOut = false;
  }

  hideChildModal(): void {
    // this.childModal.hide();
  }

  stay() {
    // this.childModal.hide();
    this.reset();
  }

  // logout() {
  //   // this.childModal.hide();
  //   // this.appService.setUserLoggedIn(false);
  //   // this.router.navigate(['/']);
  // }

  passdata:any;
  Logout(){

    this.passdata = {
      'UserId': localStorage.getItem("userId"),
      'Event':'LOGOUT'
  }
  var data = {
    "data": {
      "content": btoa(JSON.stringify(this.passdata)),
      "encryptCode": ""
      },
      "globalInfo": {
      "latitude": "2.32.34.00",
      "langitude": "12.232.12",
      "deviceNo": "Hp",
      "userName": localStorage.getItem("username"),
      "requestTime": new Date(),
      "interfaceCode": "I014",
      "UserCategory": localStorage.getItem("userCategory"),
      "userId": localStorage.getItem("userId")
      },
      "returnStateInfo": {
      "returnMessage": "",
      "returnCode": ""
      }
   }
    this.service.logout(data).subscribe((res:any) =>{
      var data = JSON.parse(res);
      if(data.returnStateInfo.returnMessage = 'Success')
        {
           var msg = atob(data.data.content)

            // this.dialog.openDialog(msg,'Success');
            localStorage.removeItem('userId');
            localStorage.removeItem('token');
            this.router.navigate(['/login']);
            this.dialog.openDialog('Session timeout','Alert');
        }
        else
        {
          this.dialog.openDialog(msg,'Error')
        }
    })
  }


  async loadModels() {

     const MODEL_URL_1 = './assets/models/'
    const MODEL_URL = 'https://raw.githubusercontent.com/justadudewhohacks/face-api.js/master/weights'

    // await faceapi.loadFaceLandmarkModel(MODEL_URL)
    // await faceapi.loadFaceRecognitionModel(MODEL_URL)
    // await faceapi.loadFaceExpressionModel(MODEL_URL)
     await faceapi.loadMtcnnModel(MODEL_URL_1),
    await faceapi.nets.ssdMobilenetv1.loadFromUri(MODEL_URL),
    await faceapi.nets.tinyFaceDetector.loadFromUri(MODEL_URL),
    await faceapi.nets.faceLandmark68Net.loadFromUri(MODEL_URL),
    await  faceapi.nets.faceRecognitionNet.loadFromUri(MODEL_URL),
    await faceapi.nets.faceExpressionNet.loadFromUri(MODEL_URL)
    // await faceapi.nets.tinyFaceDetector.loadFromUri(MODEL_URL)

     this.toastr.success('Models Loaded!');

  }


}

