export default " \n    .terminal {\n        position: relative;\n        padding-top: 30px;\n        border-radius: 6px;\n        margin-top: 8px;\n        overflow: hidden;\n        background-color: rgb(15, 15, 16);\n        width: 400px;\n        height: 300px;\n        margin-top: 5%;\n    }\n  \n  \n    /* resize images */\n  \n  \n    .terminal img {\n        width: 100%;\n        height: auto;\n    }\n  \n  \n    .terminal::before {\n        content: \"\\2022 \\2022 \\2022\";\n        position: absolute;\n        top: 0;\n        left: 0;\n        height: 4px;\n        background: rgb(58, 58, 58);\n        color: #c2c3c4;\n        width: 100%;\n        font-size: 2rem;\n        line-height: 0;\n        padding: 14px 0;\n        text-indent: 4px;\n    }\n  \n  \n    .terminal pre {\n        font-family: SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;\n        color: white;\n        padding: 0 1rem 1rem;\n        margin: 0;\n    }\n  \n  \n    /* Responsive Styles */\n  \n  \n    @media screen and (max-width: 767px) {\n  \n        .card-container>*:not(.circle-link),\n        .terminal {\n            width: 100%;\n        }\n  \n        .card:not(.highlight-card) {\n            height: 16px;\n            margin: 8px 0;\n        }\n  \n        .card.highlight-card span {\n            margin-left: 72px;\n        }\n  \n        svg#rocket-smoke {\n            right: 120px;\n            transform: rotate(-5deg);\n        }\n    }\n  \n  \n    @media screen and (max-width: 575px) {\n        svg#rocket-smoke {\n            display: none;\n            visibility: hidden;\n        }\n    }\n/*# sourceMappingURL=data:application/json;base64, */"