import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { NgxSpinnerService } from 'ngx-spinner';
import { DialogService } from 'src/app/services/dialog.service';
import { FetchapiService } from 'src/app/services/fetchapi.service';
import {computeGlobal} from 'src/app/components/computegrid/globalvar';

@Component({
  selector: 'app-computegrid',
  templateUrl: './computegrid.component.html',
  styleUrls: ['./computegrid.component.css']
})
export class ComputegridComponent implements OnInit {

  computegridForm:FormGroup;
  ComputeLists : any;
  constructor(public fb: FormBuilder,private spinner: NgxSpinnerService,public dialog:DialogService,
    private service:FetchapiService,
    public modal: MatDialog,private dialogRef: MatDialogRef<ComputegridComponent>,
      @Inject(MAT_DIALOG_DATA) public data:any) {debugger 
      
        
      }

      ngOnInit(){debugger
        this.reactiveForm();
        this.ComputeLists = computeGlobal.ComputeLists
      }
    
      reactiveForm(){
        this.computegridForm = this.fb.group({
          DocId:[0],
          Comptag: ['', [Validators.required]],
          TagFormula:['', [Validators.required]],
          
        })
      }


     
      AddCompute(){debugger
        var passdata ={
          'Comptag': this.computegridForm.value.Comptag,
          'TagFormula':this.computegridForm.value.TagFormula
        }
        computeGlobal.ComputeLists.push(passdata)
        this.ComputeLists = computeGlobal.ComputeLists
        this.computegridForm.reset();
        //this.ComputeLists.push(passdata)
      }


      delectedItem(index){debugger
        //this.ComputeLists.splice(index, 1);
        computeGlobal.ComputeLists.splice(index, 1);
        this.ComputeLists = computeGlobal.ComputeLists
      }

      submit() {debugger
        //this.dialogRef.close(this.ComputeLists);
        this.dialogRef.close(computeGlobal.ComputeLists);
      }

      CloseCoputepage(){
        this.dialogRef.close(computeGlobal.ComputeLists);
      }
      OpenHelp(){debugger
        // this.dialog.openDialog('<div><span>Decimal formula : #d3-1_2$+#d3-1_5$+#d3-1_8$</span><br><span>DateTime formula : #dt_2$&#tm_2$-#dt_1$&#tm_1$</span><br><span>Time formula : #tm_2$-#tm_1$</span></div>','Help');
        this.dialog.openDialog('<div><div class="var"><h4>1)&nbsp;Decimal formula</h4><span>Ex: #d3-1_2$+#d3-1_5$+#d3-1_8$</span></div><br><div class="var"><h4>2)&nbsp;DateTime formula</h4><span>Ex: #dt_2$&#tm_2$-#dt_1$&#tm_1$</span></div><br><div class="var"><h4>3)&nbsp;Time formula</h4><span>Ex: #tm_2$-#tm_1$</span></div></div>','Help');
      }
}
