  protected filterBanks(dropvalue,listttt) { debugger
    let search = dropvalue.value;
    if (!search) {
      listttt.next(this.POList.slice());
      return;
    } else {
      search = search.toLowerCase();
    }
    // filter the banks
    listttt.next(
      this.POList.filter(item => item.PO_No.toLowerCase().indexOf(search) > -1)
    );
  }

  protected filterItem(dropvalue,listttt) { debugger
    let search = dropvalue.value;
    if (!search) {
      listttt.next(this.ItemList.slice());
      return;
    } else {
      search = search.toLowerCase();
    }
    // filter the banks
    listttt.next(
      this.ItemList.filter(item => item.itemName.toLowerCase().indexOf(search) > -1)
    );
  }

  protected filterBatch(dropvalue,listttt) { debugger
    let search = dropvalue.value;
    if (!search) {
      listttt.next(this.BatchList.slice());
      return;
    } else {
      search = search.toLowerCase();
    }
    // filter the banks
    listttt.next(
      this.BatchList.filter(item => item.batchName.toLowerCase().indexOf(search) > -1)
    );
  }

