import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { VideoComponent } from './video/video.component'
import{ImageComponentComponent} from './image-component/image-component.component'
import { TakesnapComponent } from './takesnap/takesnap.component';
import { FullComponent } from './layouts/full/full.component';
import { PolistComponent } from './components/polist/polist.component';
import { CreatepoComponent } from './components/createpo/createpo.component';
import { UserprofileComponent } from './components/userprofile/userprofile.component';
import { PagelistComponent } from './components/pagelist/pagelist.component';
import { SectionthreeComponent } from './components/sectionthree/sectionthree.component';
import { CreatedocumentComponent } from './components/createdocument/createdocument.component';
import { UploadpageComponent } from './components/uploadpage/uploadpage.component';
import { LoginComponent } from './components/login/login.component';
import { RecordFaceComponent } from './components/record-face/record-face.component';
import { MobilerechargeComponent } from './components/mobilerecharge/mobilerecharge.component';
import { RechargeconfirmationComponent } from './components/rechargeconfirmation/rechargeconfirmation.component';
import { ConfirmationreceiptComponent } from './components/confirmationreceipt/confirmationreceipt.component';
import { ElectricityComponent } from './components/electricity/electricity.component';

const routes: Routes = [
  {  path: '', component: FullComponent,
    children: [
      {  path: '', redirectTo: '/login',  pathMatch: 'full'  } ,
      
      {  path: 'polist',       component: PolistComponent   },
      {  path: 'userprofile',  component: UserprofileComponent  },
      {  path: 'pagelist/:Id', component: PagelistComponent  },
      {  path: 'sectionthree/:Id/:Id2', component: SectionthreeComponent  },
      {  path: 'createpo',     component: CreatepoComponent  },
      {  path: 'createdoc',    component: CreatedocumentComponent  },
      {  path: 'uploadpage',   component: UploadpageComponent  },
      {  path: 'recordface',   component:RecordFaceComponent},
      {  path: 'mobilerecharge',   component:MobilerechargeComponent},
      {  path: 'rechargeconfirmation',   component:RechargeconfirmationComponent},
      {  path: 'confirmationreceipt',   component:ConfirmationreceiptComponent},
      {  path: 'electricity',   component:ElectricityComponent}



      ]
    },
      {  path:'login', component:LoginComponent },
      { path: 'video', component: VideoComponent },
      { path: 'snap', component: TakesnapComponent },
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
