getFaceData()
{debugger

  var data = {
    "data": {
      "content": "",
      "encryptCode": ""
    },
    "globalInfo": {
      "latitude": "2.32.34.00",
      "langitude": "12.232.12",
      "deviceNo": "Hp",
      "userName": "",
      "requestTime": new Date(),
      "interfaceCode": "I024",
      "UserCategory": "",
      "userId": ""
    },
    "returnStateInfo": {
      "returnMessage": "",
     "returnCode": ""
    }
  }

  Loading.show();

  this.service.getFaceData(data).subscribe((res:any) =>{debugger
  
  var data = JSON.parse(res);
  var faceArray = JSON.parse(atob(data.data.content));
      faceArray = faceArray.records;
  //  alert(data.returnStateInfo.returnMessage)


   if(data.returnStateInfo.returnMessage == 'Success')
   {
    if(faceArray.length > 0)
    {
      for(var i = 0 ; i < faceArray.length ; i++)
      {
    var float32Array =  new Float32Array(JSON.parse(faceArray[i].FaceArray));
    var arr = [];
    arr[0] = float32Array;

    var labeledFaceDescriptor = new faceapi.LabeledFaceDescriptors(faceArray[i].UserId.trim(), arr);
    this.faceDescriptor.push(labeledFaceDescriptor);

    }
    }

   }
   else
   {
     this.dialog.openDialog('Not getting face detecrors list','Error')
   }

   Loading.hide();

  });

  getset.faceData =  this.faceDescriptor;

}
