import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup, FormGroupDirective, NgForm, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { ErrorStateMatcher, MatDialog, MAT_DIALOG_DATA } from '@angular/material';
// import { minLength } from '@rxweb/reactive-form-validators';

import { NgxSpinnerService } from 'ngx-spinner';
import { DialogService } from 'src/app/services/dialog.service';
import { FetchapiService } from 'src/app/services/fetchapi.service';
import { UserprofileComponent } from '../userprofile/userprofile.component';


export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const invalidCtrl = !!(control && control.invalid && control.parent.dirty);
    const invalidParent = !!(control && control.parent && control.parent.invalid && control.parent.dirty);

    return (invalidCtrl || invalidParent);
  }
}
interface keyObject {
  [key: string]: any
}
@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.css']

})
export class ChangePasswordComponent implements OnInit {

  changePassword: FormGroup;
  matcher = new MyErrorStateMatcher();
  Response: any
  validation: any
  minlength: any;
  uppercase: any;
  lowercase: any;
  specialchar: any;
  minimumlength: any;
  passRequirement: any
  pattern: any
  constructor(
    private formBuilder: FormBuilder,
    public dialog: DialogService,
    private spinner: NgxSpinnerService,
    private service: FetchapiService,
    public modal: MatDialog,
    public userProfile: UserprofileComponent,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    this.createForm();
  }

  ngOnInit() {
    this.validatePass();
  }

  validatePass() {
    debugger

    var data = {
      "data": {
        "content": "",
        "encryptCode": ""
      },
      "globalInfo": {
        "latitude": "2.32.34.00",
        "langitude": "12.232.12",
        "deviceNo": "Hp",
        "userName": localStorage.getItem("username"),
        "requestTime": new Date(),
        "interfaceCode": "I017",
        "UserCategory": localStorage.getItem("userCategory"),
        "userId": localStorage.getItem("userId")
      },
      "returnStateInfo": {
        "returnMessage": "",
        "returnCode": ""
      }
    }

    this.spinner.show();
    this.service.ValidatePassword(data).subscribe((res: any) => {
      debugger
      this.spinner.hide();
      var data = JSON.parse(res);
      this.Response = JSON.parse(atob(data.data.content));

      this.validationcase();

    });
  }

  validationcase() {
    debugger
    let minimumlength = this.changePassword.get('new_pass').value;
    const validresult = this.Response.Pwdvalidate
    const min = new RegExp(`(?=([^a-z]*[a-z])\{${validresult.minLowerChar},\})`)
    const cap = new RegExp(`(?=([^A-Z]*[A-Z])\{${validresult.minUpperChar},\})`)
    const spl = new RegExp(`(?=(\.\*[\$\@\$\!\%\*\?\&])\{${validresult.minSplChar},\})`)
    if (validresult.minLength && validresult.minLowerChar && validresult.minUpperChar && validresult.minSplChar) {
      this.changePassword.get('new_pass').setValidators([Validators.required,Validators.compose([Validators.minLength(validresult.minLength),
      ChangePasswordComponent.patternValidator(min, { hasSmallCase: true }),
      ChangePasswordComponent.patternValidator(cap, { hasCapitalCase: true }),
      ChangePasswordComponent.patternValidator(spl, { hasSpecialCharacters: true }),
      ])]);
      this.changePassword.get('new_pass').updateValueAndValidity();

    }

  }

  static patternValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      if (!control.value) {
        return null;
      }
      const valid = regex.test(control.value);
      return valid ? null : error;
    };
  }

  checkPasswords(group: FormGroup) { // here we have the 'passwords' group
    let pass = group.controls.new_pass.value;
    let confirmPass = group.controls.confirm_pass.value;

    return pass === confirmPass ? null : { notSame: true }
  }

  changePass(items: any) {
    debugger

    if (!this.changePassword.valid) {
      return false;
    }

    var a = {
      'UserId': localStorage.getItem("userId"),
      'Pwd': items.new_pass,
      'OldPwd': items.old_pass
    }

    var enc = btoa(JSON.stringify(a));

    var data = {
      "data": {
        "content": enc,
        "encryptCode": ""
      },
      "globalInfo": {
        "latitude": "2.32.34.00",
        "langitude": "12.232.12",
        "deviceNo": "Hp",
        "userName": localStorage.getItem("username"),
        "requestTime": new Date(),
        "interfaceCode": "I016",
        "UserCategory": localStorage.getItem("userCategory"),
        "userId": localStorage.getItem("userId")
      },
      "returnStateInfo": {
        "returnMessage": "",
        "returnCode": ""
      }
    }

    this.spinner.show();
    this.service.ChangePassword(data).subscribe((res: any) => {
      debugger
      this.spinner.hide();
      var data = JSON.parse(res);
      var R = atob(data.data.content);
      if (data.returnStateInfo.returnMessage == 'Success') {
        if (JSON.parse(R).responseModel.status == true) {
          this.modal.closeAll();
          this.dialog.openDialog(JSON.parse(R).responseModel.msg, 'Success')
          return true;
        }
        else {
          this.dialog.openDialog(JSON.parse(R).responseModel.msg, 'Error')
        }

      }
      else {
        this.dialog.openDialog(data.returnStateInfo.returnMessage, 'Error')
      }
    });

  }
  createForm() {
    debugger
    
    this.changePassword = this.formBuilder.group({
      old_pass: ['', Validators.required],
      new_pass: [''],
      confirm_pass: ['', Validators.required],
    }, { validator: this.checkPasswords }
    );

  }

  cancelModal() {
    this.modal.closeAll();
  }

}
